"use client"

import { useState, useEffect, useContext } from "react"
import axios from "axios"
import { AuthContext } from "../context/AuthContext"
import { API_URL } from "../config"
import AdminTicketList from "../components/tickets/AdminTicketList"

const AdminDashboard = () => {
  const { user } = useContext(AuthContext)
  const [tickets, setTickets] = useState([])
  const [loading, setLoading] = useState(true)
  const [error, setError] = useState(null)

  // Fetch all tickets
  useEffect(() => {
    const fetchTickets = async () => {
      try {
        const config = {
          headers: {
            Authorization: `Bearer ${user.token}`,
          },
        }

        const res = await axios.get(`${API_URL}/api/tickets`, config)
        setTickets(res.data)
        setLoading(false)
      } catch (err) {
        setError("Failed to fetch tickets")
        setLoading(false)
      }
    }

    fetchTickets()
  }, [user])

  // Update ticket status
  const updateTicketStatus = async (ticketId, status) => {
    try {
      const config = {
        headers: {
          Authorization: `Bearer ${user.token}`,
          "Content-Type": "application/json",
        },
      }

      const res = await axios.put(`${API_URL}/api/tickets/${ticketId}`, { status }, config)

      // Update tickets in state
      setTickets(tickets.map((ticket) => (ticket._id === ticketId ? { ...ticket, status: res.data.status } : ticket)))

      return true
    } catch (err) {
      return false
    }
  }

  return (
    <div className="container mx-auto px-4 py-8">
      <h1 className="mb-8 text-2xl font-bold text-gray-900 dark:text-white">All Support Tickets</h1>

      {loading ? (
        <div className="flex h-40 items-center justify-center">
          <p>Loading tickets...</p>
        </div>
      ) : error ? (
        <div className="rounded-md bg-red-50 p-4 dark:bg-red-900/30">
          <p className="text-sm text-red-700 dark:text-red-200">{error}</p>
        </div>
      ) : (
        <AdminTicketList tickets={tickets} updateTicketStatus={updateTicketStatus} />
      )}
    </div>
  )
}

export default AdminDashboard

